@import '~xtend-library/src/core/link/link-setup.less';

/**
 * decoration
 */

.link--underline() {
  text-decoration: underline;
  &:hover, &.hover, &:active, &.active {
    text-decoration: underline;
  }
}

.link--none() {
  text-decoration: none;
  &:hover, &.hover, &:active, &.active {
    text-decoration: none;
  }
}

/**
 * text--inverse
 */

.text--inverse {
  // link color
  a:not(.btn), .btn--link {
    color: @text--inverse;
    text-decoration: underline;
    &:hover, &.hover, &:active, &.active {
      color: @text--inverse--dark;
    }
  }
}

/**
 * link
 */

a:not(.btn), .btn--link {
  .link--underline();
  // styles
  color: @primary;
  &:hover, &.hover, &:active, &.active {
    color: @primary--dark;
  }
  // animation
  transition-property: color, background, opacity;
  transition-duration: @time--medium;
  transition-timing-function: @bez--out;
  &:hover, &.hover, &:active, &.active {
    transition-property: color, background, opacity;
    transition-duration: @time--medium;
    transition-timing-function: @bez--in;
  }
}

/**
 * caret
 */

.caret {
  .btn--flex();
  > span {
    .icon-xt(@content: @icon-xt--chevron-down, @size: @icon--small);
    &:after {
      .icon--right();
    }
  }
  // activation
  &.active {
    > span {
      .icon-xt(@content: @icon-xt--chevron-up, @size: @icon--small);
      &:after {
        .anim-y-inverse.active();
      }
    }
  }
  &.out {
    > span {
      &:after {
        .anim-y.active();
      }
    }
  }
  // inverse
  &.caret--inverse {
    > span {
      .icon-xt(@content: @icon-xt--chevron-up, @size: @icon--small);
    }
    &.active {
      > span {
        .icon-xt(@content: @icon-xt--chevron-down, @size: @icon--small);
        &:after {
          .anim-y.active();
        }
      }
    }
    &.out {
      > span {
        &:after {
          .anim-y-inverse.active();
        }
      }
    }
  }
  // horizontal
  &.caret--horizontal {
    > span {
      .icon-xt(@content: @icon-xt--chevron-right, @size: @icon--small);
    }
    &.active {
      > span {
        .icon-xt(@content: @icon-xt--chevron-left, @size: @icon--small);
        &:after {
          .anim-x-inverse.active();
        }
      }
    }
    &.out {
      > span {
        &:after {
          .anim-x.active();
        }
      }
    }
    // inverse
    &.caret--inverse {
      > span {
        .icon-xt(@content: @icon-xt--chevron-left, @size: @icon--small, @position: before);
        &:after {
          content: '';
        }
        &:before {
          .icon--left();
        }
      }
      &.active {
        > span {
          .icon-xt(@content: @icon-xt--chevron-right, @size: @icon--small, @position: before);
          &:before {
            .anim-x.active();
          }
        }
      }
      &.out {
        > span {
          &:before {
            .anim-x-inverse.active();
          }
        }
      }
    }
  }
}
